body {
  color: #333;
  background-color: #fff;
  flex-direction: column;
  min-height: 100vh;
  font-family: Arial, sans-serif;
  line-height: 1.6;
  display: flex;
}

header {
  color: #fff;
  background-color: #4caf50;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  display: flex;
  position: relative;
}

header img {
  width: 80px;
  height: auto;
  position: absolute;
  top: 50%;
  left: 1rem;
  transform: translateY(-50%);
}

header h1 {
  text-align: center;
  color: #000;
  margin: 0;
  font-size: 2rem;
}

header p {
  font-size: 1.1rem;
}

nav ul {
  background-color: #423f3f;
  justify-content: center;
  list-style-type: none;
  display: flex;
}

nav ul li {
  margin: 0 10px;
}

nav ul li a {
  color: #fff;
  padding: 10px 15px;
  text-decoration: none;
  display: block;
}

nav ul li a:hover {
  background-color: #4caf50;
}

main {
  background-color: #fff;
  max-width: 900px;
  margin: auto;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

main img {
  border-radius: 8px;
  max-width: 100%;
  height: auto;
  margin: 1rem auto;
  display: block;
}

main ul {
  padding: 0;
  list-style-type: none;
}

main ul li {
  background-color: #fff;
  border-left: 4px solid #ffeb3b;
  margin-bottom: 10px;
  padding: 10px;
  transition: background-color .3s;
}

main ul li:hover {
  background-color: #4caf4f5e;
}

main ul li a {
  color: #4caf50;
  font-weight: bold;
  text-decoration: none;
}

h2 {
  color: #4caf50;
  margin-top: 1.5rem;
  font-size: 1.3rem;
}

p, ul li {
  margin-bottom: 1rem;
}

#map1, #map2, #map3 {
  width: 100%;
  height: 400px;
  margin-top: 20px;
}

@media (width <= 768px) {
  nav ul {
    flex-direction: column;
  }

  nav ul li {
    text-align: center;
    margin: 0;
  }

  header h1 {
    font-size: 1.5rem;
  }

  header p {
    font-size: 1rem;
  }
}

@media (width <= 400px) {
  header {
    justify-content: flex-end;
  }
}

footer {
  color: #fff;
  text-align: center;
  background-color: #423f3f;
  margin-top: auto;
  padding: 20px;
}

footer img {
  width: 80px;
  height: auto;
  margin-bottom: 10px;
}

footer nav ul {
  justify-content: center;
  gap: 15px;
  padding: 0;
  list-style-type: none;
  display: flex;
}

footer nav ul li a {
  color: #fff;
  font-weight: bold;
  text-decoration: none;
  transition: color .3s;
}

footer p {
  margin-top: 10px;
  font-size: .9rem;
}

.clipped-img {
  clip-path: polygon(0 25%, 35% 25%, 35% 0%, 65% 0%, 65% 25%, 100% 24%, 100% 63%, 65% 63%, 65% 100%, 35% 100%, 35% 62%, 0 63%);
}

@keyframes cambiarColores {
  0% {
    filter: hue-rotate();
  }

  50% {
    filter: hue-rotate(180deg);
  }

  100% {
    filter: hue-rotate(360deg);
  }
}

#escudo {
  width: 200px;
  height: auto;
  animation: 2s infinite cambiarColores;
}

#logo {
  width: 60px;
  height: auto;
}
